import CatalogoControler from "./CatalogoControler"
import { Essencia,Carvao,Aluminio,Acessorio } from "./Classes"
import ProdutosDB from "./DataBases/Produtos.json"

console.log("iniciando controlers")

ProdutosDB.ESSENCIAS.forEach((Value)=>{
    CatalogoControler.AdicionarProduto(
        new Essencia(
            {
                'DinPix': Value.Valor,
                'Cart': Value.Valor
            },
            Value["Tem"],
            Value["Marca"],
            Value["Sabor"],
            Value.Nome,
            9999,
            Value.Descricao,
            {},
            Value.Imagem
        )
    )
})
ProdutosDB.CARVAO.forEach((Value)=>{
    CatalogoControler.AdicionarProduto(
        new Carvao(
            Value.Valor,
            Value.Tem,
            Value.Marca,
            Value.Nome,
            Value.Descricao,
            Value.Imagem
        )
    )
})
ProdutosDB.ALUMINIO.forEach((Value)=>{
    CatalogoControler.AdicionarProduto(
        new Aluminio(
            Value.Valor,
            Value.Tem,
            Value.Marca,
            Value.Nome,
            Value.Descricao,
            Value.Imagem
        )
    )
})
ProdutosDB.ACESSORIOS.forEach((Value)=>{
    CatalogoControler.AdicionarProduto(
        new Acessorio(
            Value.Valor,
            Value.Cor,
            Value.Marca,
            Value.Especificacao,
            Value.Tem,
            Value.Descricao,
            Value.Imagem
        )
    )
})

